import React, {useEffect, useState} from "react";
import Tabletop from "tabletop";
import './App.css';
import logo from './static/logo.png';

export default function App() {
    const [data, setData] = useState([]);
    const [reopenedTheaters, setReopenedTheaters] = useState();
    const [admissions, setAdmissions] = useState();
    const [europeanMovies, setEuropeanMovies] = useState();
    const [period, setPeriod] = useState();

    useEffect(() => {
        Tabletop.init({
            key: "1l-yq6MX8Egp5ITuKxmVl0LXUc2TeMYaGW6urqXTKSQ8",
            callback: getData
        })
    }, []);

    useEffect(() => {
        if (data && data[0] && data[0]["Reopened Theaters"]) {
            setReopenedTheaters(buildNumbers("Reopened Theaters", 5));
        }
        if (data && data[0] && data[0]["Number of admissions"]) {
            setAdmissions(buildNumbers("Number of admissions", 8));
        }
        if (data && data[0] && data[0]["Number of European movies"]) {
            setEuropeanMovies(buildNumbers("Number of European movies", 5));
        }
        if (data && data[0] && data[0]["Period"]) {
            setPeriod(data[0]["Period"]);
        }
    }, [data]);

    function getData(data, tabletop) {
        const kinoData = tabletop.sheets("kino_de").elements;
        setData(kinoData);
    }

    function buildNumbers(key, length) {
        const number = data[0][key];
        return padWithZeroes(number, length);
    }

    function padWithZeroes(number, length) {
        let zeros = '';
        while (number.length + zeros.length < length) {
            zeros = '0' + zeros;
        }
        return {zeros, number};
    }

    return (
        <div className={"widget-container"}>
            <div className={"background-stars"} />
            <div className={"logo"}><img alt={"Zurück ins Kino"} src={logo}/></div>
            <div className={"sections"}>
                <div className="section">
                    <div className={"heart"}>
                        <div className="reopened-theaters-numbers">
                            <span className={"padding-zeros"}>{reopenedTheaters && reopenedTheaters.zeros}</span>
                            <span className={"number"}>{reopenedTheaters && reopenedTheaters.number}</span>
                        </div>
                    </div>
                    <div className={"subtitle subtitle-heart"}>
                        Kinos haben wieder für dich geöffnet
                    </div>
                </div>
                <div className="section">
                    <div className={"visitors"}>
                        <div className="admissions-numbers">
                            <span className={"padding-zeros"}>{admissions && admissions.zeros}</span>
                            <span className={"number"}>{admissions && admissions.number}</span>
                        </div>
                    </div>
                    <div className={"subtitle subtitle-visitors"}>
                        Kinofans zurück im Kino
                    </div>
                </div>
                <div className={"section"}>
                    <div className="clap">
                        <div className="european-movies-numbers">
                            <span className={"padding-zeros"}>{europeanMovies && europeanMovies.zeros}</span>
                            <span className={"number"}>{europeanMovies && europeanMovies.number}</span>
                        </div>
                    </div>
                    <div className={"subtitle subtitle-clap"}>
                        Filme aus Europa im Kino gestartet
                    </div>
                </div>
            </div>
            <div className={"footer"}>
                <div className="period">
                    <span>1. Mai 2021 – </span>
                    <span>{period}</span>
                </div>
                <div className={"comscore"}>
                    powered by comscore, inc.
                </div>
            </div>
        </div>
    );
}
